<template>
  <ul class="user-box-main">
    <li v-for="(list, i) in data" :key="i">
      <el-button @click="addUser('uid', $event)">
        <img src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg" />
        <span v-if="i%3 === 1">用户名称很长的话用户名称很长的话用户名称很长的话用户名称很长的话用户名称很长的话用户名称很长的话</span>
        <span v-else>用户名称</span>
      </el-button>
    </li>
  </ul>
</template>

<script>
export default {
  name: "people",
  data() {
    return {
      tempUid: 0
    };
  },
  props: {
    data: null
  },
  methods: {
    addUser(uid, e) {
      let that = this;
      let elm = e.currentTarget;
      elm.disabled = true;
      that.tempUid++;
      let newUser = {
        username: "用户" + "1000" + that.tempUid,
        avatar: require("../assets/img/avatar.jpg"),
        uid: "1000" + that.tempUid
      };
      let users = that.$store.state.Selected;
      users.splice(users.length, 0, newUser);
      that.$store.dispatch("setSelectedFun", users);
    }
  }
};
</script>